* {
    padding: 0;
    margin: 0;
    font-family: "Jost", sans-serif;
   
}

body {
        background: lightsteelblue;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        height: 100vh;
        width: 100vw;
        font-family: "Jost", sans-serif;
}

html {
    font-size: 100%;
    height: 100%;
    background: #eee;
}

.box {
    width: auto;
    height: auto;
    border-radius: 1.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background:lightsteelblue;
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
    min-width: 20rem;
    min-height: 35rem;
}

.wave {
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
}

.wave.-one{

    animation: drift 9000ms infinite linear;
    z-index: 1 !important;
    opacity: 0.1;
}
.wave.-three {
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}

.wave.-two{

    animation: drift 3000ms infinite linear;
    z-index: 3 !important;
    opacity: 0.1;
}

.box::after{
    content: "";
    display: block;
    left: 0%;
    top: 0%;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0, 0, 0);

}

@keyframes drift {
    from { 
        transform: rotate(0deg);
    }
    from { 
        transform: rotate(360deg);
    }
  }
 

.inputData{

    width: 100%;
    position: relative;
    display: flex;
    padding-top: 4%;
}

.inputFeild{

    width: 50%;
    box-sizing: border-box;
    border: 3px lightgreen;
    margin: auto;
    border-radius: 5px 5px 5px 5px;
    outline: none;
    color: #9DBFAF;
    height : 4vh;
    

}

.info{
    padding: 15% 0% 0% 15%;
}

.location{

    font-size: 50px;

}
